.App{
    max-width: 1200px;
    width: 100%;
}
.App-header {
    background-color: #222;
    padding: 20px;
    color: white;
    text-align: center;
}

.App-title {
    font-size: 1.5em;
    padding: 20px 0;
    font-weight: bold;
}

.App-title a{
    color: #ffffff;
}
.App-intro {
    font-size: 15px;
    padding: 0 25px;
}

html, body {
    color: black;
}

*:not('#mkdbuttons') {
    margin: 0;
    padding: 0;
}

#wrapper {
    font: 15px helvetica, arial, freesans, clean, sans-serif;
    -webkit-font-smoothing: antialiased;
    line-height: 1.7;
    padding: 3px;
    background: #fff;
    border-radius: 3px;
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
}

p {
    margin: 1em 0;
}

a {
    color: #4183c4;
    text-decoration: none;
}

#wrapper {
    background-color: #fff;
    padding: 30px;
    margin: 15px;
    font-size: 15px;
    line-height: 1.6;
}

#wrapper > *:first-child {
    margin-top: 0 !important;
}

#wrapper > *:last-child {
    margin-bottom: 0 !important;
}

@media screen {
    #wrapper {
        -webkit-box-shadow: 0 0 0 1px #cacaca, 0 0 0 4px #eee;
                box-shadow: 0 0 0 1px #cacaca, 0 0 0 4px #eee;
    }
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 700;
    line-height: 1.7;
    cursor: text;
    position: relative;
    margin: 1em 0 15px;
    padding: 0;
}

h1 {
    font-size: 2.5em;
    border-bottom: 1px solid #ddd;
}

h2 {
    font-size: 2em;
    border-bottom: 1px solid #eee;
}

h3 {
    font-size: 1.5em;
}

h4 {
    font-size: 1.2em;
}

h5 {
    font-size: 1em;
}

h6 {
    color: #777;
    font-size: 1em;
}

p, blockquote, table, pre {
    margin: 15px 0;
}

ul {
    padding-left: 30px;
}

ol {
    padding-left: 30px;
}

ol li ul:first-of-type {
    margin-top: 0px;
}

hr {
    background: transparent url(data:image/png;base64,) repeat-x 0 0;
    border: 0 none;
    color: #ccc;
    height: 4px;
    margin: 15px 0;
    padding: 0;
}

#wrapper > h2:first-child {
    margin-top: 0;
    padding-top: 0;
}

#wrapper > h1:first-child {
    margin-top: 0;
    padding-top: 0;
}

#wrapper > h1:first-child + h2 {
    margin-top: 0;
    padding-top: 0;
}

#wrapper > h3:first-child, #wrapper > h4:first-child, #wrapper > h5:first-child, #wrapper > h6:first-child {
    margin-top: 0;
    padding-top: 0;
}

a:first-child h1, a:first-child h2, a:first-child h3, a:first-child h4, a:first-child h5, a:first-child h6 {
    margin-top: 0;
    padding-top: 0;
}

h1 + p, h2 + p, h3 + p, h4 + p, h5 + p, h6 + p, ul li > :first-child, ol li > :first-child {
    margin-top: 0;
}

dl {
    padding: 0;
}

dl dt {
    font-size: 14px;
    font-weight: bold;
    font-style: italic;
    padding: 0;
    margin: 15px 0 5px;
}

dl dt:first-child {
    padding: 0;
}

dl dt > :first-child {
    margin-top: 0;
}

dl dt > :last-child {
    margin-bottom: 0;
}

dl dd {
    margin: 0 0 15px;
    padding: 0 15px;
}

dl dd > :first-child {
    margin-top: 0;
}

dl dd > :last-child {
    margin-bottom: 0;
}

blockquote {
    border-left: 4px solid #DDD;
    padding: 0 15px;
    color: #777;
}

blockquote > :first-child {
    margin-top: 0;
}

blockquote > :last-child {
    margin-bottom: 0;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    font-size: 100%;
    font: inherit;
}

table th {
    font-weight: bold;
    border: 1px solid #ccc;
    padding: 6px 13px;
}

table td {
    border: 1px solid #ccc;
    padding: 6px 13px;
}

table tr {
    border-top: 1px solid #ccc;
    background-color: #fff;
}

table tr:nth-child(2n) {
    background-color: #f8f8f8;
}

img {
    max-width: 100%;
}

code, tt {
    margin: 0 2px;
    padding: 0 5px;
    white-space: nowrap;
    border: 1px solid #eaeaea;
    background-color: #f8f8f8;
    border-radius: 3px;
    font-family: Consolas, 'Liberation Mono', Courier, monospace;
    font-size: 12px;
    color: #333333;
}

pre > code {
    margin: 0;
    padding: 0;
    white-space: pre;
    border: none;
    background: transparent;
}

.highlight pre {
    background-color: #f8f8f8;
    border: 1px solid #ccc;
    font-size: 13px;
    line-height: 19px;
    overflow: auto;
    padding: 6px 10px;
    border-radius: 3px;
}

pre {
    background-color: #f1f2f7;
    border: 1px solid #ccc;
    font-size: 15px;
    line-height: 19px;
    overflow: auto;
    border-radius: 3px;
    color: #000000;
    margin: 26px 0;
    display: inline-block;
    padding: 5px 40px 5px 18px;
}

pre code, pre tt {
    background-color: transparent;
    border: none;
}

.poetry pre {
    font-family: Georgia, Garamond, serif !important;
    font-style: italic;
    font-size: 110% !important;
    line-height: 1.6em;
    display: block;
    margin-left: 1em;
}

.poetry pre code {
    font-family: Georgia, Garamond, serif !important;
    word-break: break-all;
    word-break: break-word;
    /* Non standard for webkit */
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
        hyphens: auto;
    white-space: pre-wrap;
}

sup, sub, a.footnote {
    font-size: 1.4ex;
    height: 0;
    line-height: 1;
    vertical-align: super;
    position: relative;
}

sub {
    vertical-align: sub;
    top: -1px;
}

@media print {
    body {
        background: #fff;
    }

    img, pre, blockquote, table, figure {
        page-break-inside: avoid;
    }

    #wrapper {
        background: #fff;
        border: none;
    }

    pre code {
        overflow: visible;
    }
}

@media screen {
    body.inverted {
        color: #eee !important;
        border-color: #555;
        -webkit-box-shadow: none;
                box-shadow: none;
    }

    .inverted #wrapper, .inverted hr, .inverted p, .inverted td, .inverted li, .inverted h1, .inverted h2, .inverted h3, .inverted h4, .inverted h5, .inverted h6, .inverted th, .inverted .math, .inverted caption, .inverted dd, .inverted dt, .inverted blockquote {
        color: #eee !important;
        border-color: #555;
        -webkit-box-shadow: none;
                box-shadow: none;
    }

    .inverted td, .inverted th {
        background: #333;
    }

    .inverted pre, .inverted code, .inverted tt {
        background: #eeeeee !important;
        color: #111;
    }

    .inverted h2 {
        border-color: #555555;
    }

    .inverted hr {
        border-color: #777;
        border-width: 1px !important;
    }

    ::-moz-selection {
        background: rgba(157, 193, 200, 0.5);
    }

    ::selection {
        background: rgba(157, 193, 200, 0.5);
    }

    h1::-moz-selection {
        background-color: rgba(45, 156, 208, 0.3);
    }

    h1::selection {
        background-color: rgba(45, 156, 208, 0.3);
    }

    h2::-moz-selection {
        background-color: rgba(90, 182, 224, 0.3);
    }

    h2::selection {
        background-color: rgba(90, 182, 224, 0.3);
    }

    h3::-moz-selection, h4::-moz-selection, h5::-moz-selection, h6::-moz-selection, li::-moz-selection, ol::-moz-selection {
        background-color: rgba(133, 201, 232, 0.3);
    }

    h3::selection, h4::selection, h5::selection, h6::selection, li::selection, ol::selection {
        background-color: rgba(133, 201, 232, 0.3);
    }

    code::-moz-selection {
        background-color: rgba(0, 0, 0, 0.7);
        color: #eeeeee;
    }

    code::selection {
        background-color: rgba(0, 0, 0, 0.7);
        color: #eeeeee;
    }

    code span::-moz-selection {
        background-color: rgba(0, 0, 0, 0.7) !important;
        color: #eeeeee !important;
    }

    code span::selection {
        background-color: rgba(0, 0, 0, 0.7) !important;
        color: #eeeeee !important;
    }

    a::-moz-selection {
        background-color: rgba(255, 230, 102, 0.2);
    }

    a::selection {
        background-color: rgba(255, 230, 102, 0.2);
    }

    .inverted a::-moz-selection {
        background-color: rgba(255, 230, 102, 0.6);
    }

    .inverted a::selection {
        background-color: rgba(255, 230, 102, 0.6);
    }

    td::-moz-selection, th::-moz-selection, caption::-moz-selection {
        background-color: rgba(180, 237, 95, 0.5);
    }

    td::selection, th::selection, caption::selection {
        background-color: rgba(180, 237, 95, 0.5);
    }

    .inverted {
        background: #0b2531;
        background: #252a2a;
    }

    .inverted #wrapper {
        background: #252a2a;
    }

    .inverted a {
        color: #acd1d5;
    }
}

